

  
  .toolbar {
    display: flex;
    justify-content: start;
    padding: 10px;
    background-color: #f8f8f8;
    border-bottom: 1px solid #ccc;
  }

  .copy-form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    margin: 3rem 6rem;
}

.copy-form > div {
    display: grid;
    width: 15rem;
    margin-bottom: .5rem;
}

@media(max-width: 600px) {
    .copy-form {
        margin: 3rem 1rem;
    }
}

.listRowDiv {
  display: flex;
  width: 80em;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 0.5em;
}


.noPadding::part(content){
  padding: 0px;
}